<template>
    <div class="container">
        <PageLoadding v-if="show"></PageLoadding>
    </div>
</template>

<script>
    import PageLoadding from "@/components/PageLoadding.vue";
    import { companyShortName } from "@/tool/common/config.js";
    import Mixins from './Mixins.js'

    export default {
        mixins:[ Mixins ],
        components: {
            PageLoadding
        },
        data(){
            return {
                name:'cp',
                method: 'getCpapiUrl',
            }
        },
        created() {
            this.config.gameId = this.$route.params.id
        },
        methods: {
                    
            getCpapiUrl(){
                this.show= true
                let data = {
                    ...this.config,
                    merchantName: 'mmzb',
                    clientType: 2,
                }
                this.$api.links.getJbUrl(data)
                .then(res=>{
                    console.log(res);
                    this.show= false
                    if(res.result==1){
                        if(!this.debug){
                            window.location.href = res.data
                        }
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

